import {
    getAllExamType,
    getAllQuestionsType,
    getAllSubject,
    addQuestions,
    updateQUestions,
    getQusetions
} from '@/service/questionsManger'
import UtilGlobal from '../../utils/global'
function requestData(){
    return Promise.all([getAllExamType(),getAllQuestionsType(),getAllSubject()]);
}
export default {
    namespace:'addQuestions',
    state:{
        examType:[],
        exam_id:'',
        questionsType:[],
        questions_type_id:'',
        subject:[],
        subject_id:'',
        title:'', //题目标题
        questions_stem:'', // 题干
        questions_answer:'',// 答案
        addStatus:0, // 0 未提交 1提交成功 2 提交中 3 提交失败
        updatedStatus:0
    },
    reducers:{
        initData(state,payload){ // 初始化数据
            return {...state,...payload}
        }
    },
    effects:{
        *getQuestions(payload, {call,put}){
            console.log(payload)
            let result = yield call(getQusetions, {
                questions_id: payload.id
            });
            if(result.code === 1){
                yield put({type:'initData',...result.data[0]});
            }
        },
        *requestData(action,{call,put,select}){
            let state = yield select(store=>store.addQuestions);
            let result = yield call(requestData);
            let keys = ['examType','questionsType','subject'];
            let ids = ['exam_id','questions_type_id','subject_id']
            let payload = {};
            result.forEach((item,key)=>{
                if(item){
                    payload[keys[key]] = item.data;
                    if(!state[ids[key]]){
                        payload[ids[key]] = item.data[0][ids[key]];
                    }
                }
            });
            let localData = JSON.parse(localStorage.getItem('editQuestionsInfo'));
            if(localData){
                payload = {...payload,...localData}
            }
            yield put({type:'initData',...payload});
        },
        *addQuestions(payload,{call,put}){
            let timer = setTimeout(()=>{
                UtilGlobal.changeState({isOpen:true});
            },500);
            yield put({type:'initData',addStatus:2});
            let result = yield call(addQuestions,payload);
            clearTimeout(timer);
            UtilGlobal.changeState({isOpen:false});
            if(result.code === 1){
                yield put({type:'initData',addStatus:1});
            }else{
                yield put({type:'initData',addStatus:3});
            }
        },
        *updateQUestions(payload,{call,put}){
            let timer = setTimeout(()=>{
                UtilGlobal.changeState({isOpen:true});
            },500);
            yield put({type:'initData',updatedStatus:2});
            let {
                title,
                questions_stem,
                questions_answer,
                subject_id,
                questions_type_id,
                exam_id,
                questions_id
            } = payload;
            let result = yield call(updateQUestions,{
                title,
                questions_stem,
                questions_answer,
                subject_id,
                questions_type_id,
                exam_id,
                questions_id
            });
            clearTimeout(timer);
            UtilGlobal.changeState({isOpen:false});
            if(result.code === 1){
                yield put({type:'initData',updatedStatus:1});
            }else{
                yield put({type:'initData',updatedStatus:3});
            }
        }
    }

}