import request from '../utils/request'
// 获取所有的考试类型
export function getAllExamType(){
    return request('/exam/examType');
}
// 获取所有的试题类型
export function getAllQuestionsType(){
    return request('/exam/getQuestionsType');
}
// 获取所有的课程
export function getAllSubject(){
    return request('/exam/subject');
}
// 添加试题
export function addQuestions(questionsInfo){
    return request('/exam/questions',{
        method:'POST',
        body:questionsInfo,
    });
}
// 获取试题
export function getQusetions(payload){
    let arr = [];
    for(let i in payload){
        if (payload[i]) {
            arr.push(`${i}=${payload[i]}`);
        }
    }
    let str = '?'+arr.join('&');
    console.log(str)
    return request(`/exam/questions/condition${str}`);
}

// 更新试题

export function updateQUestions(updated){
    return request('/exam/questions/update',{
        method:'PUT',
        body:updated
    });
}